import DocInfo from './docInfo'
import HWPHeader from './header'
import Section from './section'

class HWPDocument {
  header: HWPHeader

  info: DocInfo

  sections: Section[]

  constructor(
    header: HWPHeader,
    info: DocInfo,
    sections: Section[],
  ) {
    this.header = header
    this.info = info
    this.sections = sections
  }
}

export default HWPDocument
